.home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.home__container {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    border-bottom: 1px solid #000;
}

.swipere {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0 5rem;


}

.slider-nav {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    z-index: 2;
    display: flex;
    justify-content: space-between;
}

.slider-nav i {
    font-size: 5rem;
    padding: 0.5rem;
    cursor: pointer;
    color: #fff;
}

.swipere-slide {
    position: relative;
    text-align: left;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: -2;
}



.headerr h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    color: #ffffcf;
    letter-spacing: 1rem;
}


.swipere-slide img {
    /* display: block;
    width: 200vw;
    height: 80vh;
    object-fit: cover;
    z-index: -2; */
}


.slider-copy {
    position: relative;
    left: 50%;
    text-align: center;
    color: #fff;
    z-index: 1;
    width: 100%;
    transform: translateX(-50%);



}


.home-details {
    position: absolute;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    text-align: left;
    gap: 1rem;
    margin-bottom: 3rem;
}

.swipere__p {
    width: 100%;
    color: #ffffff;
    font-size: 1rem;
    font-family: "Satoshi-Bold";
    letter-spacing: 0.525rem;
    text-transform: uppercase;
    display: flex;
    align-items: start;
    justify-content: flex-start;
    text-align: left;

}

.swipere__p p {
    border: 1px solid var(--container-color);
    padding: 0 1rem;
    border-radius: 2rem;
    background-color: var(--container-color);
    color: var(--nav-overlay-color);
}

.home-details h2 {
    display: inline-block;
    position: relative;
    font-size: 4.5rem;
    color: #fffce2;
    font-family: "Satoshi-Black";
    font-weight: var(--font-bold);
    text-transform: uppercase;
    letter-spacing: 0.525rem;
    text-align: left;
}

.home-details h2::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #fff;
    /* Replace with your desired border color */
}

.swipere {

    padding: 10rem;
}


.swipere-slide img {
    width: 80%;
    height: 80vh;
    object-fit: cover;
    z-index: -2;
    padding: 2rem 0;

}

.main__carousel {
    position: relative;
}

.main__carousel .swiper-button-prev {
    left: 13rem;
    top: 25rem;
    color: #fff;

}

.main__carousel .swiper-button-next {
    right: 13rem;
    top: 25rem;
    color: #fff;
}

.main__carousel .swiper-pagination-bullet {
    background-color: #7e7e7e;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    position: absolute;
    top: 30rem;
}


.main .swiper-pagination-bullet-active {
    background-color: var(--title-color);
}


@media (max-width: 450px) {

    .slider-nav i {
        font-size: 1rem;
        padding: 0.5rem;
        cursor: pointer;
        color: #fff;
    }


    
    .home-details h2 {
        display: inline-block;
        position: relative;
        font-size: 1.2rem;
        color: #fffce2;
        font-family: "Satoshi-Black";
        font-weight: var(--font-bold);
        text-transform: uppercase;
        letter-spacing: 0.225rem;
        text-align: left;
    }
    .swipere-slide img {
        width: 100%;
        height: 40vh;
        object-fit: cover;
        z-index: -2;
        padding: 2rem 0;
        border-top: 0px solid #000;;
        
    
    }

    .home__container {
        border-bottom: 1px solid #000;
    }

    .swipere {
        padding: 2rem;
    }

    .main__carousel .swiper-button-prev {
        position: absolute;
        left: 0.7rem;
        top: 10rem;
        color: #fff;
    
    }
    
    .main__carousel .swiper-button-next {
        right: 0.7rem;
        top: 10rem;
        color: #fff;
    }
    
    
}